<template>
  <div v-if="active">
    <label>OperatorId:</label><br/>
    <select v-model="operatorId">
      <option value="1">1</option>
      <option value="2">2</option>
      <option value="3">3</option>
      <option value="4">4</option>
      <option value="5">5</option>
    </select>

    <div class="card" v-for="(ship, index) in ships" :key="index">
      <div class="row">
        <div class="img-box">
          <p>{{ ship.shipName }}</p>
        </div>
        <div class="infoBox">
          <p class="shipTitle">Ship id: {{ ship.shipId }}</p>
          <p class="shipTitle">Ship name: {{ ship.shipName }}</p>
          <p class="shipTitle">Ship description: {{ ship.shipDesc }}</p>
        </div>
      </div>
      <div class="clock-in">
       <p @click="clockIn(ship.shipId)">Clock In</p>
      </div>
    </div>
  </div>
  <div v-else>
    <h1>{{ message }}</h1>
  </div>
</template>

<script>
export default {
  name: "ShipsComponent",

  data(){
    return {
      ships: [
        {
          shipId: '1',
          shipName: 'Titanic',
          shipDesc: 'This is a safe ship'
        },
        {
          shipId: '2',
          shipName: 'The Mayflower',
          shipDesc: 'Beautiful ship'
        },
        {
          shipId: '3',
          shipName: 'Cruise Ship 1997',
          shipDesc: 'A luxurious ship'
        }
      ],
      operatorId: '',
      active: true,
      message: ''
    }
  },
  methods: {
    clockIn(shipId){
      this.active = false
      this.message = 'Operator: ' + this.operatorId + ' clocked in at ShipId: ' + shipId
    }
  }

}
</script>

